.Ecommerce{
    color: white;
    /* margin: 10px 30px 10px 10px; */
}

.Ecommerce #firstLine{
    display: flex;
    position: relative;
    /* left: 50px; */
    top: 10vh;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 100px;
}


.Ecommerce #firstLine #Earnings{
    background-color: white;
    width: 25pc;
    height: 200px;
    color: rgb(87, 85, 85);
    border-radius: 20px;
}

#EarningFirst{
    display: flex;
    justify-content:space-between;
    align-items: center;
    margin:30px 20px;
    
}

#EarningFirst h3{
    margin: 0;
}

#EarningFirst h2{
    color: grey;
    font-weight: 400;
    margin: 0;
}

#EarningFirst p{
    border-radius: 50%;
    width: 40px;
    margin: 0;
    margin-right: 20px;
    padding: 8px;
    text-align: center;
    color: white;
    font-size: 35px;
    cursor: pointer;
}

#Earnings button{
    cursor: pointer;
    color: white;
    padding: 8px;
    text-align: center;
    border-radius: 10px;
    border: none;
    margin-left: 60px;
    font-size: 20px;
}

#Assets{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 0px;
}

.AssetsCards{
    width: 15pc;
    height: 160px;
    /* background-color:rgba(255, 255, 255, 0.158); */
    background-color:var(--NavbarSideH3);
    margin-bottom: 20px;
    border-radius: 20px;
    margin-right: 10px;
    
}

#recentTranscation{
    width: 500px;
    height: 100%;
    /* display: flex;
    flex-direction: column;
    justify-content: space-between; */
    border-radius: 20px;
    /* margin-top: 120px; */
    margin: 120px auto 0px;
    background-color: var(--NavbarSideH3);
    padding: 20px 10px;
}

#priceTranscation{
    color: #1DA348;
    font-weight: 700;
    font-size: 18px;
    text-align: end;
}

.profileTopicEcomm{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 50px;
}

.profileTopicEcomm #profileContainer{
    display: flex;
    gap: 20px;
}

.profileTopicEcomm svg{
    font-size: 50px;
    border-radius: 10px;

}

#recentTranscation h3,#recentTranscation p, .profileTopicEcomm{
    margin: 0px;
}

.AssetsCards h2{
    display: flex;
    align-items: center;
    margin: 0;
    font-weight: 400;
    margin-left: 15px;
}

.AssetsCards h3{
    margin: 0;
    font-weight: 200;
    margin-left: 15px;
    font-size: 15px;
}

#AssetLogo1{
    background-color: #E5FAFB;
    font-size: 45px;
    padding: 8px;
    border-radius: 50%;
    color: #03C9D7;
    margin: 30px  0px 0px 20px;
    cursor: pointer;
}

#AssetLogo2{
    background-color: #FEC90F;
    font-size: 45px;
    padding: 8px;
    cursor: pointer;
    border-radius: 50%;
    margin: 30px  0px 0px 20px;
}

#AssetLogo3{
    background-color: #FFF4E5;
    cursor: pointer;
    font-size: 45px;
    padding: 8px;
    border-radius: 50%;
    margin: 30px  0px 0px 20px;
    color: #EB8C91;
}

#AssetLogo4{
    background-color: #EBFAF2;
    cursor: pointer;
    font-size: 45px;
    padding: 8px;
    border-radius: 50%;
    color: #00C292;
    margin: 30px  0px 0px 20px;
}

.AssetsCards #span1, #span4{
    color: red;
    font-size:15px;
    margin-left: 10px;
}

.AssetsCards #span2 ,#span3{
    color: #2A944A;
    font-size:15px;
    margin-left: 10px;
}
@media only screen and (max-width:580px){
    #recentTranscation{
        width: 400px;
        /* margin-left: 10px;
        margin-right: 10px; */
    }
    .profileTopicEcomm{
        padding-left: 0;
        padding-right: 0;
    }
}

@media only screen and (max-width:496px){
    .AssetsCards{
        width:10pc;
    }
    .Ecommerce #firstLine #Earnings{
        /* margin-right: 5px; */
        width: 300px;
    }
    #recentTranscation{
        width: 250px;
        height: 100%;
    }
}