.Customers {
  color: rgb(0, 0, 0);
  background-color: #ffffff;
  padding: 40px;
  border-radius: 20px;
  margin: 20px 70px 20px 50px;
}

.Customers h3 {
  color: rgb(136, 124, 124);
  margin: 0;
  font-weight: 400;
}
.Customers h2 {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  /* color: rgb(184, 169, 169); */
}

.Customers table {
  width: 100%;
}

.Customers table td {
  padding: 10px 10px 3px 10px;
  text-align: center;
  /* height: 100px; */
  /* border-bottom: 1px solid rgba(0, 0, 0, 0.364); */
}

.Customers tr:hover {
  background-color: #d6cdcd5d;
}
/* 
